.SkillsSection {
  @media screen and (max-width: 1100px) {
    padding: 4px 12px;
  }
}

.skills-container {
  .title-wrap {
    display: flex;
    align-items: center;
  }
  .skill-title {
    font-size: 1.4rem;
    font-weight: 400;
  }
  .skill-logo {
    width: 42px;
    height: 42px;
    margin-right: 8px;

    @media screen and (max-width: 780px) {
      width: 32px;
      height: 32px;
    }
  }

  @media screen and (max-width: 1100px) {
    .skill-title {
      font-size: 1rem;
    }
  }

  .skill-bar {
    display: flex;
    align-items: center;
    .skill-text {
      font-weight: bold;
      margin-left: 10px;
      margin-top: 4px;

      @media screen and (max-width: 780px) {
        margin-left: 5px;
      }
    }
    .skill-progress {
      width: 100%;
      .progress {
        width: 100%;
        height: 0.5rem;
        background-color: red;
        margin-left: 1rem;
        background-color: #ee53539f;
        position: relative;
        .inner-pregress {
          position: absolute;
          background-color: crimson;
          height: 100%;
          bottom: 0;
          left: 0;
        }

        @media screen and (max-width: 1100px) {
          max-width: 400px;
        }
      }
    }
  }
}
