$mainColor: #15023a;

$width: 768px;
$width2: 1130px;
$width3: 512px;

html {
  scroll-behavior: smooth;
}

@mixin mobile {
  @media (max-width: #{$width}) {
    @content;
  }
}

@mixin xmobile {
  @media (max-width: #{$width3}) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: #{$width2}) {
    @content;
  }
}
